/**
 * The list of file extensions that are allowed to be uploaded in a Markdown editor.
 * Extensions must be lower-case without leading dots.
 * NOTE: Has to be kept in sync with the server-side definitions in FileService.java
 */
export const UPLOAD_MARKDOWN_FILE_EXTENSIONS = ['png', 'jpg', 'jpeg', 'gif', 'svg', 'pdf'];

/**
 * The global list of file extensions that are allowed to be uploaded.
 * Extensions must be lower-case without leading dots.
 * NOTE: Has to be kept in sync with the server-side definitions in FileService.java
 */
export const UPLOAD_FILE_EXTENSIONS = [
    'png',
    'jpg',
    'jpeg',
    'gif',
    'svg',
    'pdf',
    'zip',
    'tar',
    'txt',
    'rtf',
    'md',
    'htm',
    'html',
    'json',
    'doc',
    'docx',
    'csv',
    'xls',
    'xlsx',
    'ppt',
    'pptx',
    'pages',
    'pages-tef',
    'numbers',
    'key',
    'odt',
    'ods',
    'odp',
    'odg',
    'odc',
    'odi',
    'odf',
];

export const ALLOWED_FILE_EXTENSIONS_HUMAN_READABLE = UPLOAD_FILE_EXTENSIONS.join(', ');
// The list of file extensions for the "accept" attribute of the file input field
export const ACCEPTED_FILE_EXTENSIONS_FILE_BROWSER = UPLOAD_FILE_EXTENSIONS.map((ext) => '.' + ext).join(',');

